import Nav from "../components/Nav";
import IDCard from "../components/IDCard";
import Banner from "../components/Banner";
import SettingsBtn from "../components/SettingsBtn";
import pic from "../image/ticket.png";

function Ticket() {
  return (
    <>
      <Nav parameter={2} />
      <div className="main">
        <img src={pic} alt="" />
        <h1 style={{position:"absolute", top:"500px", textAlign:"center"}}>
                "아따 시발 날씨도 좋은데 <br/>소주나 빨자잉"

        </h1>
        <SettingsBtn color={"red"} />
      </div>
    </>
  );
}

export default Ticket;
